@import url("https://fonts.cdnfonts.com/css/muli");

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

a {
  text-decoration: none;
  color: inherit;
}

a:hover {
  color: inherit;
}

/* custom scrollbar */
::-webkit-scrollbar {
  width: 20px;
}

::-webkit-scrollbar-track {
  background-color: transparent;
}

::-webkit-scrollbar-thumb {
  background-color: #d6dee1;
  border-radius: 20px;
  border: 6px solid transparent;
  background-clip: content-box;
}

::-webkit-scrollbar-thumb:hover {
  background-color: #a8bbbf;
}

.secondary-text {
  color: #9b9b9b;
  font-size: 16px;
}

.card1 {
  padding: 40px 30px;
  border-radius: 8px;
  box-shadow: 0 0 8px 1px rgba(228, 228, 228, 0.5);
  background-color: #fff;
}

.row {
  margin: 0;
  padding: 0;
}

/* ---------- */
/* Login Page */
/* ---------- */

.PhoneInputInput {
  border: none;
}
.PhoneInputInput:focus {
  border: none;
  box-shadow: none;
  outline: none;
}

.email-text1 {
  display: block;
  font-size: 16px;
  font-weight: 600;
}
.email-text2 {
  font-size: 16px;
  font-weight: 800;
}
.email-text3 {
  font-size: 14px;
}

.login-container {
  height: 100vh;
  width: 100%;
  overflow: hidden;
  font-size: 18px;
}

.left-side {
  padding: 0;
  background-color: var(--color_p1);
  position: relative;
}
.right-side {
  padding: 0;
  overflow-y: scroll;
  height: 100%;
}

.login-img {
  width: 100%;
  height: 100vh;
  object-fit: cover;
  object-position: center -90px;
  mix-blend-mode: soft-light;
}

.login-footer {
  position: absolute;
  bottom: 0;
  color: #fff;
  padding: 0 50px;
  margin-bottom: 50px;
}

.quote {
  font-size: 22px;
  font-weight: 200;
  margin-right: 20%;
}

.open-quote {
  font-size: 80px;
  position: absolute;
  top: -60px;
  height: 50px;
}
.close-quote {
  font-size: 80px;
  position: absolute;
  bottom: 20px;
  right: calc(20% + 20px);
  height: 50px;
}

.author {
  margin-top: 10px;
  font-size: 24px;
  text-transform: uppercase;
}

.top-left {
  position: absolute;
  top: 0;
  color: #fff;
  font-size: 22px;
  padding: 20px 50px;
  z-index: 99;
}

.three-dots {
  position: relative;
  top: 15px;
  height: 0;
  text-align: center;
  color: #fff;
  font-size: 10px;
}

.form-container {
  margin: auto;
  padding: 20px;
  width: 50%;
  display: flex;
  flex-direction: column;
}

.submit-btn {
  background-color: var(--color_p2);
  border: var(--color_p2) 2px solid;
  height: 50px;
  color: #fff;
}
.submit-btn:hover {
  background-color: #fff;
  border: var(--color_p2) 2px solid;
  color: var(--color_p2);

  /* background-color: var(--color_p1);
    border: var(--color_p1) 2px solid; */
}

.create-btn {
  background-color: #e543c3;
  border: #e543c3 2px solid;
  height: 50px;
  color: #fff;
  margin: 30px 0px;
  padding-left: 120px;
  padding-right: 120px;
  margin-left: 40px;
}
.create-btn:hover {
  background-color: #fff;
  border: #e543c3 2px solid;
  color: #e543c3;
}

.form-logo {
  width: 250px;
  margin-bottom: 30px;
}

.alternate-option {
  color: var(--color_p2);
  margin-top: 30px;
}

.alternate-option b {
  cursor: pointer;
}

