/*  */
.grid-container {
  display: flex;
  flex-wrap: wrap;
  padding: 0 4px;
}

.column {
  flex: 20%;
  max-width: 25%;
  padding-left: 10px;
}

.column img {
  margin-bottom: 10px;
  vertical-align: middle;
  width: 100%;
  opacity: 0.3;
  border-radius: 2px;
}

.web1-image {
  position: relative;
}

.web1-image:hover img {
  opacity: 1;
  transition: 0.3s;
}
.web1-image:hover .image_overlay {
  opacity: 1;
  transition: 0.3s;
}

.image_overlay {
  position: absolute;
  bottom: 10px;
  width: 100%;
  height: 100%;
  padding: 20px 15px;
  opacity: 0;
  color: #fff;
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0), #000);
  display: flex;
  flex-direction: column;
  justify-content: end;
}

.overlay-name {
  font-size: 20px;
}

.overlay-profession {
  font-size: 14px;
  font-weight: 200;
}

.invalid-otp {
  color: red;
  display: none;
}
.correct-otp {
  color: green;
  display: none;
}

